import React,{useState} from 'react'
import {Flex,Heading,Text,Input,Textarea,FormControl,FormErrorMessage,FormHelperText,FormLabel,Image,Stack,InputGroup,InputRightAddon} from "@chakra-ui/react"
import {  useToast,Button,Box,Grid} from "@chakra-ui/react"
import { Link } from '@chakra-ui/react'
import { ExternalLinkIcon } from '@chakra-ui/icons'
import {FaLinkedinIn} from 'react-icons/fa'
import {SiGmail} from 'react-icons/si'
import {BsGithub} from 'react-icons/bs'

const Contact = () => {
  const myFunction=()=>{
    window.open("mailto:kavanish011@gmailcom")
  }
  const [data, setData] = useState({
    name:"",
    email:"",
    subject:"",
    message:""
   
    
  })
  const toast = useToast()
  const handleInputChange = (e) =>{
    const{name,value}=e.target
    setData({...data,[name]:value})
  } 
  const isError = data.name||data.email === ''
  const {name,email,subject,message}=data

  const HandleSubmit=()=>{
    if(name===''||email===''||message===''||subject ===""){
      alert("fill all data")
      return
    }
  
    toast({
      title: 'Message Recieved',
      description: "We will contact you soon .",
      status: 'success',
      duration: 1000,
      isClosable: true,
    })
    setData({
      name:"",
      email:"",
      subject:"",
      message:""
     
    })
  }
 
  return (
    
    <Box id='Contact'  w='100%' h='auto' >
    
    <Heading textAlign="center" m={10} >-Contact Me-</Heading>
   
    <Flex justifyContent='space-around'direction={{base:"column",lg:"row"}} pl={9} pr={12}  alignItems="center"  >

      <Flex gap={3} direction="column"  >
      <Heading textAlign="center" m={10} color='#DC143C'>-Get in tuch-</Heading>
        <Heading as="h5" size="lg" >
        Have a web design project in mind?
        </Heading>
        <Heading as="h5" size="lg" >
        Let's discuss it !
        </Heading>
        <Heading as='h2'  color='#DC143C'>Mo.9696492862</Heading>
        <Flex justifyContent='space-around' gap="30px"  margin='auto'>
         
        <div className="sub-title1" onClick={()=>myFunction()}> <Text  cursor={'pointer'} fontSize='2xl'color='##00'>
        <Image src={'https://cdn-icons-png.flaticon.com/128/5968/5968534.png'} alt="github" w='40%'/>
           </Text></div>


          <Link href='https://www.linkedin.com/in/avnish-kumar' isExternal>
          <Image src={'https://cdn-icons-png.flaticon.com/128/145/145807.png'} alt="linkdin" w='40%'/>
          </Link>

          <Link href='https://github.com/Ashukla011' isExternal>
          <Image src={'https://cdn-icons-png.flaticon.com/128/1051/1051326.png'} alt="github" w='40%'/>
          </Link>

          </Flex>
       
        
        
      </Flex>
      
      <Box >

    <Grid gap={4} gridTemplateColumns={{base:"repeat(1,1fr)",md:"repeat(2,1fr)"}}>
    
     <FormControl isInvalid={isError}>
      <FormLabel >Full Name</FormLabel>
      <Input type='text' value={name} onChange={handleInputChange} placeholder="Name" name="name"  errorBorderColor='blue'/>
     
      {!isError ? (
        <FormHelperText >
         Enter the full name
        </FormHelperText>
      ) : (
        <FormErrorMessage color={'#DC143C'}>Name is required.</FormErrorMessage>
      )}
    </FormControl>
   
    <FormControl isInvalid={isError}>
      <FormLabel >Email</FormLabel>
      <Input type='email' value={email} onChange={handleInputChange} placeholder="Email" name="email"  errorBorderColor='blue'/>
      {!isError ? (
        <FormHelperText>
          Enter the email you'd like to receive the newsletter on.
        </FormHelperText>
      ) : (
        <FormErrorMessage color={'#DC143C'}>Email is required.</FormErrorMessage>
      )}
    </FormControl>
    <FormControl isInvalid={isError}>
    <FormLabel>Subject</FormLabel>
      <Input type='text' value={subject} onChange={handleInputChange} placeholder="Subject" name="subject"  errorBorderColor='blue'/>
     
    </FormControl>
    <FormControl isInvalid={isError}>
    <FormLabel >Message</FormLabel>
      <Textarea type='text' value={message} onChange={handleInputChange} placeholder="Message.." name="message"  errorBorderColor='blue'/>
     
    </FormControl>
    
  <Button backgroundColor='#92bcb5' onClick={HandleSubmit}>Submit</Button>
      </Grid>
      </Box>
    </Flex>
    </Box>
  
  )
}

export default Contact

