// import Head from 'next/head';
import {
  Box,
  Heading,
  Container,
  Text,
  Button,
  Stack,

} from '@chakra-ui/react';
import Avnish_Kumar_Resume from '../Resume/Avnish_Kumar_Resume.pdf'
export default function CallToActionWithAnnotation() {
  return (
    <>
      

      <Container maxW={'3xl'}>
        <Stack
          as={Box}
          textAlign={'center'}
          spacing={{ base: 8, md: 14 }}
          py={{ base: 20, md: 36 }}>
          <Heading
            fontWeight={600}
            fontSize={{ base: '2xl', sm: '4xl', md: '6xl' }}
            lineHeight={'110%'}>
           I am Avnish Kumar  <br />
            <Text as={'span'} color={'#DC143C'}>
           MERN Developer
            </Text>
          </Heading>
          <Text color={'gray.500'} fontSize='md'>
        An enthusiastic motivated and innovative aspiring full-stack web developer. 
        Looking career with a progressive organization to capitalize my skills and
         abilities in the eld of information technology.
        Eager to learn new technologies and methodologies.
          </Text>
          <Stack
            direction={'column'}
            spacing={3}
            align={'center'}
            alignSelf={'center'}
            position={'relative'}>
                <a href={Avnish_Kumar_Resume} download={'Avnish_Kumar_Resume'}>
            <Button
              colorScheme={'black'}
              bg={'#000'}
              rounded={'full'}
              px={6}
              _hover={{
                bg:'#DC143C',
              }}>
              Resume
            </Button>
            </a>
            <Box>
              
            </Box>
          </Stack>
        </Stack>
      </Container>
    </>
  );
}
